<script>
  import {mapGetters,mapActions} from 'vuex'
  import {imageUrl} from '../../config/index.js'
  // 这里可以导入其他文件（比如：组件，工具 js，第三方插件 js，json 文件，图片文件等等）
  import loginHeader from './loginHeader'
  import {getRegister,getSendCode} from "../../api/index.js";
  export default {
    // import 引入的组件需要注入到对象中才能使用
    components: {
      loginHeader
    },
    props: {},
    name: "register",
    data: function () {
      return {
        headerName:'注册账号',
		imageUrl: imageUrl,
        data: '',
        dynamicValidateForm: {
          phone: '',
          code: '',
          pass: '',
          checkPass: ''
        }
      }
    },
    // 计算属性 类似于 data 概念
    computed: {
      ...mapGetters([
        'userPhone'
      ])
    },
    // 监控 data 中的数据变化
    watch: {},
    // 方法集合
    methods: {
      ...mapActions([
        'setUserPhone',
        'setUserId',
        'setToken',
        'setNickName',
        'setUserImageUrl'
      ]),
      getRegisterUl(){

        getRegister({
          deviceId: "string",
          password: this.dynamicValidateForm.checkPass,
          phone: this.dynamicValidateForm.phone,
          version: "string"
        }).then((res)=>{
          if(res.status===200){
			uni.setStorageSync('token', res.data.token);
			   uni.reLaunch({
				url: '../index/index'
			   });
          }
        })
      },
	  //发送验证码
	  getSendCodeUl(){
		  getSendCode({
			  
		  }).then((res)=>{
			  console.log("验证码",res)
		  })
	  }
    },
    // 生命周期 - 创建完成（可以访问当前this 实例）
    created() {
    },
    // 生命周期 - 挂载完成（可以访问 DOM 元素）
    mounted() {
    },
    beforeCreate() {
    },
    beforeMount() {
    }, // 生命周期 - 挂载之前
    beforeUpdate() {
    }, // 生命周期 - 更新之前
    updated() {
    }, // 生命周期 - 更新之后
    beforeDestroy() {
    }, // 生命周期 - 销毁之前
    destroyed() {
    }, // 生命周期 - 销毁完成
    activated() {
    } // 如果页面有 keep-alive 缓存功能,这个函数会触发
  }
</script>

